const baseAPI = 'https://pro-api.coinmarketcap.com/';
const apiVersion = '/v2/';

export const apiEndPoints = {
  marketStatus: 'market-status',
  marketTicker: 'tickers',
  marketDepth: 'depth',
  marketTrade: 'trades',
};

export async function fetchApi(
  endPoint: string,
  payload?: {} | undefined,
  method: string = 'get',
  headers?: {},
) {
  let path = `${baseAPI}${apiVersion}${endPoint}`;
  const headersObject: any = {
    ...headers,
  };
  let request = {
    body: JSON.stringify(payload),
    headers: headersObject,
    method: method.toLowerCase(),
  };

  try {
    return fetch(path, request);
  } catch (e) {
    const stringError = e && e.toString && e.toString();
    const type =
      stringError === 'TypeError: Network request failed'
        ? 'networkError'
        : 'unknown';
    const error = {
      text: stringError,
      type,
    };
    throw error;
  }
}
